import React from 'react'
import styled from 'styled-components'
import viewersDisney from "../images/viewers-disney.png"
import viewersMarvel from "../images/viewers-marvel.png"
import viewersNational from "../images/viewers-national.png"
import viewersPixar from "../images/viewers-pixar.png"
import viewersStarWars from "../images/viewers-starwars.png"
function Viewers() {
    return (
        <Container>
            <Wrap>
                <img src={viewersDisney}/>
            </Wrap>
            <Wrap>
                <img src={viewersMarvel}/>
            </Wrap>
            <Wrap>
                <img src={viewersNational}/>
            </Wrap>
            <Wrap>
                <img src={viewersPixar}/>
            </Wrap>
            <Wrap>
                <img src={viewersStarWars}/>
            </Wrap>
        </Container>
    )
}

export default Viewers

const Container=styled.div`
width:100%;
margin:50px auto;
display:grid;
padding: 30px 0px 26px;
grid-gap:25px;
grid-template-columns: repeat(5, minmax(0, 1fr));

`
const Wrap=styled.div`
border-radius: 10px;
border:3px solid rgba(249, 249, 249,0.1);
z-index:3;
width:150px;
box-shadow:rgb(0 0 0 / 69%) 0px 26px 30px -10px , rgba(0 0 0 / 73%) 0px 16px 10px -10px;
cursor: pointer;
transition: all 250ms cubic-bezier(0.25, 0.45, 0.45, 0.94) 0s;
img{
    width:100%;
    height: 100%;
    object-fit:cover ;

}
&:hover{
    box-shadow:rgb(0 0 0 / 80%) 0px 40px 58px -16px , rgba(0 0 0 / 73%) 0px 30px 22px -10px;
    transform: scaleX(1.05);
    border-color: rgba(249,249,249,0.8);
}

`